* {
    
    box-sizing: border-box;
}

.main {
    margin: 0px;
    padding: 0px;
    font-family: calibri;
    width: 100%;
    height: 100vh;
    background-image: url(./../pages/poductbg.png);
    background-size: cover;
    background-position: top left;
    position: relative;
}



.main-content{
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    
    
}

.main-img{
    width: 500px;
    height: 640px;
    margin: 20px;
}

.main-img img{
 height: 100%;
 width: 100%;
 object-fit: contain;
 object-position: center;
}

.main-text{
    width: 500px;
    margin: 20px;
}

.main-text h1{
   font-size: 3.5rem;
   line-height: 55px;
   color: #22252e;
   letter-spacing: 1px;
   font-weight: 700;
   margin: 20px;

}

.main-text p{
    font-size: 1rem;
    color: #777474;
    margin: 20px;
}

.main-login{
    width: 120Px;
    height:40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:#5a76fd ;
    color: #fff;
    box-shadow: 5px 10px 30px rgba(90, 118, 253, 0.5);
    margin: 20px;
}
.main-login:hover{
    background-color: #5a62fd;
    transition: all ease 0.3s;
}

.details{
    position: absolute;
    left:20%;
    bottom: 10px;
    transform: translateX(-20%);
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.match h3, .use h3{
    font-weight: 700;
    color: #22252e;
    font-size: 2.6rem;
    margin: 20px;
}

.match p, .use p{
    color: #777474;
    font-size: 1.1rem;
    margin: 20px;
}

.match font, .use font{
    color: #7b92fd;
    font-weight: 700;
}

/* The arrow shaped Scroll indicator */
.arrow{
    align-self: end;
    width: 50%;
    border-right: 1px solid #5a76fd;
    height: 20%;
    margin-bottom: 4em;
    position: absolute;
    bottom: 5px;
    right: 70px;
    animation: arrow-animation ease 1.5s;
}

/* Creating the little triangle on top of the scroll indicator */
.arrow::after{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 11px 11px 0px 11px;
    border-color: #5a76fd transparent transparent transparent;
    right: -0.7em;
    bottom: -2px;

}

.scroll{
    position: absolute;
    bottom: 20px;
    right: 55px;
    font-weight: 600;

}

@keyframes arrow-animation{
    0%{
        bottom: 70px;
        opacity: 0.2;
    }
    100%{
        bottom: 5px;
        opacity: 1;
    }
}

.solutions{
    background-image: url(./../pages/poductbg.png);
    


}