/* The arrow shaped Scroll indicator */
.arrow{
    -webkit-align-self: end;
            align-self: end;
    width: 50%;
    border-right: 1px solid #5a76fd;
    height: 20%;
    margin-bottom: 4em;
    position: absolute;
    bottom: 5px;
    right: 70px;
    -webkit-animation: arrow-animation ease 1.5s;
            animation: arrow-animation ease 1.5s;
}

/* Creating the little triangle on top of the scroll indicator */
.arrow::after{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 11px 11px 0px 11px;
    border-color: #5a76fd transparent transparent transparent;
    right: -0.7em;
    bottom: -2px;

}

.scroll{
    position: absolute;
    bottom: 20px;
    right: 55px;
    font-weight: 600;

}

@-webkit-keyframes arrow-animation{
    0%{
        bottom: 70px;
        opacity: 0.2;
    }
    100%{
        bottom: 5px;
        opacity: 1;
    }
}

@keyframes arrow-animation{
    0%{
        bottom: 70px;
        opacity: 0.2;
    }
    100%{
        bottom: 5px;
        opacity: 1;
    }
}

